<template>
	<div class="index-banner">
		<div class="swiper-container">
 			<div class="swiper-wrapper">
    			<div class="swiper-slide">
    				<img src="static/img/index/banner1.jpg"  width="100%"/>
    			</div>
    			<div class="swiper-slide">
    				<img src="static/img/index/nav1.jpg" width="100%" />
    			</div>
    			<div class="swiper-slide">
    				<img src="static/img/index/qing.jpg"  width="100%" />
    			</div>
  			</div>
  			 <div class="swiper-pagination diandian" ></div>
		</div>
	</div>
</template>

<script>
	import 'swiper/dist/css/swiper.css'
	import Swiper from 'swiper'
	export default{
		name:"indexHead",
		mounted(){
			var mySwiper = new Swiper('.swiper-container', {
			autoplay: {
			    disableOnInteraction: false,
			  },//可选选项，自动滑动
				pagination :{
					el: '.swiper-pagination'
				}
//				paginationType : 'bullets',
			})
		}
	}
	
	

</script>

<style>
	.index-banner{
		width: 3.75rem;
    	height: 1.24rem;
	}
	
	.swiper-slide img{
		display: block;
		width: 3.75rem;
    	height: 1.24rem;
	}
</style>


