	}
	.xinxi{
		display: flex;
		justify-content: space-around;
		background: white;
		width: 3.45rem;
		height: .47rem;
		margin-left: .19rem;
		align-items: center;
		margin-top: .1rem;
	}
	.o{
		height: .5rem;
		background: #efe27b;
		
	}
</style>


