	width: 2.85rem;
	height: .38rem;
	background: white;
margin-top: .15rem;
	margin-left: .44rem;
}
.big input:nth-child(3){
	background: #00b9a7;
	border-radius: .05rem;
	margin-top: .51rem;
	margin-left: .44rem;
	color: white;
}
.big p{
	font-size: .123rem;
	color: white;
	margin-top: .13rem;
	margin-left: 2.74rem;
}
</style>


