import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import shsm from '@/pages/shsm'
import zuhao from '@/pages/zuhao'
import my from '@/pages/my'
import dai from '@/pages/dai'
import tuijian from '@/pages/tuijian'
import baobei from '@/pages/baobei'
import denglu from '@/pages/denglu'
import fbdl from '@/pages/fabudailian'
import dlgl from '@/pages/dlgl'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component:index
    },
    {
    	path: '/shsm',
      name: 'shsm',
      component:shsm
    },
    {
    	path: '/zuhao',
      name: 'zuhao',
      component:zuhao
    },
    {
    	path: '/my',
      name: 'my',
      component:my
    },
     {
    	path: '/dai',
      name: 'dai',
      component:dai
    },{
    	path:'/tuijian',
    	name:'tuijian',
    	component:tuijian
    },
    {
    	path:'/baobei',
    	name:'baobei',
    	component:baobei
    },
    {
    	path:'/denglu',
    	name:'denglu',
    	component:denglu
    },
    {
    	path:'/fabudailian',
    	name:'fbdl',
    	component:fbdl
    },
    {
    	path:'/dlgl',
    	name:'dlgl',
    	component:dlgl
    }
     
  ]
})



